import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './NonMemberForm.css';

export default function NonMemberForm() {
  const [nom, setNom] = useState('');
  const [prenom, setPrenom] = useState('');
  const [dateNaissance, setDateNaissance] = useState('');
  const [tarif, setTarif] = useState(null);
  const [methodePaiement, setMethodePaiement] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // Calcul du tarif en fonction de la date de naissance
  useEffect(() => {
    if (dateNaissance) {
      const today = new Date();
      const birthDate = new Date(dateNaissance);
      const age = today.getFullYear() - birthDate.getFullYear();
      const monthDiff = today.getMonth() - birthDate.getMonth();
      
      // Ajustement si l'anniversaire n'est pas encore passÃ© cette annÃ©e
      const adjustedAge = (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) 
        ? age - 1 
        : age;

      if (adjustedAge < 6) {
        setTarif(0); // Gratuit pour moins de 6 ans
      } else if (adjustedAge < 18) {
        setTarif(8); // 8â¬ pour 6-18 ans
      } else {
        setTarif(10); // 10â¬ pour adultes
      }
    } else {
      setTarif(null);
    }
  }, [dateNaissance]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validation des champs requis
    if (!nom || !prenom || !dateNaissance || !methodePaiement) {
      setError("Tous les champs sont obligatoires");
      return;
    }
    
    setLoading(true);
    setError('');
    
    try {
      // Envoi des donnÃ©es au backend
      const response = await axios.post('http://localhost:4000/non-members', {
        nom,
        prenom,
        dateNaissance,
        tarif,
        methodePaiement
      });
      
      if (response.data.success) {
        // Redirection vers une page de confirmation ou d'attente
        navigate('/non-member-confirmation', { 
          state: { 
            nom, 
            prenom, 
            dateNaissance, 
            tarif, 
            methodePaiement 
          } 
        });
      }
    } catch (err) {
      setError(err.response?.data?.message || "Erreur lors de l'enregistrement");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="non-member-form">
      <h2>Formulaire pour Non-AdhÃ©rents</h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="nom">Nom*</label>
          <input
            id="nom"
            type="text"
            value={nom}
            onChange={(e) => setNom(e.target.value)}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="prenom">PrÃ©nom*</label>
          <input
            id="prenom"
            type="text"
            value={prenom}
            onChange={(e) => setPrenom(e.target.value)}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="dateNaissance">Date de naissance*</label>
          <input
            id="dateNaissance"
            type="date"
            value={dateNaissance}
            onChange={(e) => setDateNaissance(e.target.value)}
            required
          />
        </div>
        
        {tarif !== null && (
          <div className="tarif-display">
            <h3>Tarif applicable: {tarif === 0 ? 'Gratuit' : `${tarif}â¬`}</h3>
          </div>
        )}
        
        <div className="form-group payment-methods">
          <label>MÃ©thode de paiement*</label>
          <div className="radio-group">
            <label>
              <input
                type="radio"
                name="methodePaiement"
                value="CB"
                checked={methodePaiement === 'CB'}
                onChange={() => setMethodePaiement('CB')}
                required
              />
              Carte Bancaire
            </label>
            <label>
              <input
                type="radio"
                name="methodePaiement"
                value="Cheque"
                checked={methodePaiement === 'Cheque'}
                onChange={() => setMethodePaiement('Cheque')}
              />
              ChÃ¨que
            </label>
            <label>
              <input
                type="radio"
                name="methodePaiement"
                value="Especes"
                checked={methodePaiement === 'Especes'}
                onChange={() => setMethodePaiement('Especes')}
              />
              EspÃ¨ces
            </label>
          </div>
        </div>
        
        {error && <div className="error-message">{error}</div>}
        
        <div className="buttons-container">
          <button 
            type="button" 
            className="back-button"
            onClick={() => navigate('/')}
          >
            Retour Ã  l'accueil
          </button>
          <button 
            type="submit" 
            className="submit-button" 
            disabled={loading}
          >
            {loading ? 'Traitement...' : 'Valider'}
          </button>
        </div>
      </form>
    </div>
  );
}
